.comparison-toggle {
  margin-top: 24px;
}
.comparison-toggle__title {
  display: block;
  margin: 0 0 8px 0;
}
.comparison-toggle__button {
  justify-content: flex-start;
  padding-left: 50px;
}
.comparison-toggle__button span {
  margin-right: 14px;
}
.comparison-toggle__button:not(:last-child) {
  margin-bottom: 10px;
}
@media (max-width: 575.98px) {
  .comparison-toggle {
    margin-top: 20px;
  }
}

.comparison__aside {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 1px;
}
.comparison__content {
  position: relative;
}
.comparison__counter {
  width: 110px;
  position: absolute;
  text-align: center;
  bottom: 100%;
  right: 40px;
  line-height: 40px;
  margin-bottom: 30px;
  font-size: 1.124rem;
  color: #000;
}
@media (max-width: 767.98px) {
  .comparison__aside .comparison-properties {
    display: none;
  }
  .comparison__content {
    padding-top: 48px;
  }
  .comparison__counter {
    width: 100vw;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    bottom: auto;
    line-height: 46px;
    margin: 0;
    border-color: var(--color-border-1, #d9d9de);
    border-style: solid;
    border-width: 1px 0 1px 0;
    font-size: 1.375rem;
    font-weight: 700;
  }
}

.comparison-slider .slick-arrow {
  bottom: 100%;
  margin-bottom: 30px;
}
.comparison-slider .slick-arrow span {
  display: none;
}
.comparison-slider .slider__next {
  right: 0;
}
.comparison-slider .slider__prev {
  right: 150px;
}
.comparison-slider .slick-list {
  margin: 0 -10px 0 -11px;
  padding-left: 1px;
}
@media (max-width: 767.98px) {
  .comparison-slider .slick-arrow {
    width: calc(50% - 50px);
    height: 46px;
    line-height: 46px;
    border: 0;
    margin: 0 0 1px 0;
    background-color: transparent;
    border-radius: 0;
    color: var(--color-text-light, #BDBDBD);
  }
  .comparison-slider .slick-arrow .icon {
    fill: var(--color-text-light, #BDBDBD);
  }
  .comparison-slider .slick-arrow span {
    display: block;
  }
  .comparison-slider .slick-arrow[data-touch], .comparison-slider .slick-arrow[data-hover] {
    color: var(--color-accent, #2363D1);
  }
  .comparison-slider .slick-arrow[data-touch] .icon, .comparison-slider .slick-arrow[data-hover] .icon {
    fill: var(--color-accent, #2363D1);
  }
  .comparison-slider .slick-arrow.slick-disabled {
    opacity: 0;
  }
  .comparison-slider .slider__next {
    justify-content: flex-end;
  }
  .comparison-slider .slider__next .icon {
    margin: 0 0 0 6px;
  }
  .comparison-slider .slider__prev {
    justify-content: flex-start;
    right: auto;
    left: 0;
  }
  .comparison-slider .slider__prev .icon {
    margin: 0 6px 0 0;
  }
}
@media (max-width: 575.98px) {
  .comparison-slider .slick-list {
    margin: 0 -16px;
    padding: 0 8px;
  }
}

.comparison-slide {
  border-color: var(--color-border-1, #d9d9de);
  border-style: solid;
  border-width: 0 1px 1px 0;
}
.comparison-slide:first-child {
  border-width: 0 1px 1px 1px;
}
.comparison-slide__delete {
  text-align: center;
  padding: 20px 0 50px 0;
}
.comparison-slide__delete-button {
  width: 100%;
  max-width: 133px;
}
.comparison-slide .product-card {
  border-bottom: 1px solid var(--color-border-1, #d9d9de);
  flex-shrink: 0;
  padding: 0 10px;
}
.comparison-slide .product-card__container {
  border: 0;
}
.comparison-slide .product-card .button_icon {
  width: 64px;
}
@media (max-width: 1023.98px) {
  .comparison-slide__delete {
    padding: 20px 0;
  }
}
@media (max-width: 767.98px) {
  .comparison-slide {
    display: flex;
    flex-direction: column;
  }
  .comparison-slide__delete {
    order: 0;
    padding: 20px 10px;
  }
  .comparison-slide__delete-button {
    max-width: 100%;
    min-width: 0px;
  }
  .comparison-slide .product-card {
    order: 1;
    border-bottom: 0;
  }
  .comparison-slide .product-card .calc-count-block {
    display: none;
  }
  .comparison-slide .product-card .button_icon {
    flex: 1 1 100%;
  }
  .comparison-slide .product-card__container {
    padding-top: 22px;
  }
  .comparison-slide .product-card__status {
    top: auto;
    bottom: 100%;
    right: 0;
    line-height: 22px;
  }
  .comparison-slide .product-card__content {
    position: relative;
    padding: 10px 0 70px 0;
  }
  .comparison-slide .product-card__content-top {
    position: static;
  }
  .comparison-slide .product-card__index {
    bottom: calc(100% + 170px);
    top: auto;
    line-height: 22px;
  }
  .comparison-slide .product-card__title {
    margin: 0;
  }
  .comparison-slide .product-card__rating {
    top: auto;
    bottom: 0;
    left: 0;
    right: auto;
  }
  .comparison-slide .product-card .card-price {
    position: relative;
  }
  .comparison-slide .product-card .card-price__label {
    position: absolute;
    margin: 0;
    left: 0;
    top: calc(100% + 75px);
  }
  .comparison-slide .comparison-properties {
    order: 3;
  }
}
@media (max-width: 575.98px) {
  .comparison-slide {
    border-bottom: 0;
  }
  .comparison-slide__delete {
    padding: 20px 8px;
  }
  .comparison-slide .product-card {
    padding: 0 8px;
  }
  .comparison-slide .product-card__image {
    flex: 0 0 150px;
  }
  .comparison-slide .product-card__index {
    bottom: calc(100% + 150px);
  }
  .comparison-slide .product-card__content-top {
    margin-bottom: 15px;
  }
  .comparison-slide .product-card__card-price {
    margin-bottom: 15px;
  }
}

.comparison-properties {
  margin: 0;
  padding: 20px 0;
  list-style: none;
}
@media (max-width: 575.98px) {
  .comparison-properties {
    padding-bottom: 0;
  }
}

.comparison-property {
  padding: 10px 26px;
  transition: background-color 0.15s ease-out;
}
.comparison-property strong {
  display: none;
}
.comparison-property_style-2 {
  padding: 10px 20px;
  margin-right: -10px;
}
.comparison-property[data-touch], .comparison-property[data-hover] {
  background-color: var(--color-sections-bg, #F5F5F5);
}
@media (max-width: 767.98px) {
  .comparison-property {
    padding: 10px;
  }
  .comparison-property strong {
    display: block;
    margin-bottom: 6px;
  }
}
@media (max-width: 575.98px) {
  .comparison-property {
    padding: 10px 8px;
  }
}